html {
  scroll-behavior: smooth;

}

body {
  -webkit-font-smoothing: subpixel-antialiased;
}

.lpw_body_container {
  font-family: Rubik-Regular;
  font-size: 15px;
  line-height: 20px;

  .stick-plan {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 9999;
  }


  .mobile-plans {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
    max-width: 700px;
    background-color: white;
    padding: 15px;

    @media (max-width: 575.98px) {
      padding-left: 10px;
      padding-right: 10px;
    }

    @media (min-width: 768px) {
      display: none;
    }

    a {
      font-family: Rubik-Medium;
      font-size: 15px;
      color: #4E4E4E;
      text-align: center;
      background: #FFFFFF;
      border: 1px solid #4E4E4E;
      border-radius: 3px;
      height: 50px;
      margin-right: 15px;
      //margin-top: 15px;
      min-width: calc(33% - 10px);

      &:last-child {
        margin-right: 0 !important;
      }

      &:hover, &.active {
        background: #4E4E4E;
        color: #FFFFFF;
      }

      @media (max-width: 575.98px) {
        padding-left: 10px !important;
        padding-right: 10px !important;
      }
    }
  }

