          div {
            width: 50%;
            display: flex;
            flex-direction: column;

            .old {
              font-family: Rubik-Regular;
              font-size: 16px;
              color: #63666B;
              text-align: center;
              text-decoration: line-through;
              margin-bottom: 5px;
            }

            .new {
              font-family: Rubik-Medium;
              font-size: 22px;
              color: #383B4B;
              letter-spacing: -0.55px;
              text-align: center;
            }

            .button-switch {
              height: 44px;
              width: 135px;
              position: relative;
              background: #FFFFFF;
              border: 1px solid #018E37;
              border-radius: 22px;

              @media(min-width: 576px) and (max-width: 767.98px) {
                width: 120px;
              }

              .lbl-off,
              .lbl-on {
                margin-bottom: 0;
                cursor: pointer;
                display: block;
                font-family: Rubik-Medium;
                font-size: 15px;
                font-weight: 400;
                position: absolute;
                top: 10px;
                transition: opacity 0.25s ease-out 0.10s;
                text-transform: uppercase;
              }

              .lbl-off {
                right: 15px;
                top: 12px;

                @media(min-width: 576px) and (max-width: 767.98px) {
                  right: 10px;
                }
              }

              .lbl-on {
                color: #FFFFFF;
                opacity: 0;
                left: 15px;
                top: 12px;
              }

              .switch {
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                height: 0;
                //left: 0;
                line-height: 0;
                outline: none;
                //position: absolute;
                //top: 0;
                //width: 0;


                transition: all 3s linear;


                &:before,
                &:after {
                  content: '';
                  position: absolute;
                  box-sizing: border-box;
                }

                &:before {
                  border-radius: 22px;
                  //background: green;
                  height: 44px;
                  top: -1px;
                  transition: background-color 0.25s ease-out 0.10s;
                  width: 100%;
                  box-sizing: border-box;
                }

                &:after {
                  width: 28px;
                  height: 28px;
                  top: 7px;
                  border-radius: 50%;
                  background: #018E37;
                  transform: translate(0, 0);
                  transition: transform 0.25s ease-out 0.10s;
                  background: #FFFFFF;
                  //box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50);
                  box-sizing: border-box;
                  left: 10px;
                  background-image: url("../../assets/img/add-button.svg");
                  background-repeat: no-repeat;
                  background-position: center;
                }

                &:checked {

                  &:after {
                    //left: auto;
                    //right: 10px;
                    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.50);
                    transform: translate(87px, 0);
                    background-image: none;

                    @media(min-width: 576px) and (max-width: 767.98px) {
                      transform: translate(71px, 0);
                    }
                  }

                  & ~ .lbl-off {
                    opacity: 0;
                  }

                  & ~ .lbl-on {
                    opacity: 1;
                  }
                }

                &#switch-orange, &#switch-orange1, &#switch-orange2, &#switch-orange3, &#switch-orange4 {
                  &:checked:before {
                    background: #018E37;
                  }
                }

                &#switch-blue {
                  &:checked:before {
                    background: white;
                  }
                }
              }
            }


            .add-btn {
              background: #FFFFFF;
              border: 1px solid #018E37;
              border-radius: 22px;
              text-transform: uppercase;
              display: flex;
              justify-content: center;
