    .modal-dialog {
      .modal-content {
        background: #FFFFFF;
        box-shadow: 0 2px 40px 0 rgba(0, 0, 0, 0.60);
        border-radius: 15px;

        .modal-header {
          border: 0px;

          .close {
            opacity: 1;
          }
        }

        .modal-body {
          padding: 15px 50px;

          @media (max-width: 575.98px) {
            padding: 15px 20px;
          }

          .content {
            display: none;
          }

          .title-box {
            display: flex;
            margin-bottom: 20px;

            img {
              max-width: 50px;
              min-width: 50px;
              @media (max-width: 575.98px) {
                max-width: 42px;
                min-width: 42px;
              }

            }

            h1 {
              font-family: Rubik-Medium;
              font-size: 30px;
              color: #333333;
              letter-spacing: -0.56px;
              margin-left: 25px;

              @media (max-width: 575.98px) {
                font-family: Rubik-Medium;
                font-size: 25px;
                color: #333333;
                letter-spacing: -0.46px;
              }
            }
          }

          p {
            font-family: Rubik-Regular;
            font-size: 16px;
            color: #71757B;
            line-height: 25px;
            text-align: left;

            @media (max-width: 575.98px) {
              font-size: 15px;
            }
          }

          .price-row {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            align-items: center;
            margin-top: 25px;
            margin-bottom: 20px;

            @media (max-width: 575.98px) {
              margin-top: 10px;
              margin-bottom: 5px;
            }


            .col {
              padding: 7.5px;
            }

            .btn-col {
              padding: 7.5px;

              .button-switch {
                height: 44px;
                width: 135px;
                position: relative;
                background: #FFFFFF;
                border: 1px solid #018E37;
                border-radius: 22px;

                .lbl-off,
                .lbl-on {
                  margin-bottom: 0;
                  cursor: pointer;
                  display: block;
                  font-family: Rubik-Medium;
                  font-size: 15px;
                  font-weight: 400;
                  position: absolute;
                  top: 10px;
                  transition: opacity 0.25s ease-out 0.10s;
                  text-transform: uppercase;
                }

                .lbl-off {
                  right: 15px;
                  top: 12px;
                }

                .lbl-on {
                  color: #FFFFFF;
                  opacity: 0;
                  left: 15px;
                  top: 12px;
                }

                .switch {
                  -webkit-appearance: none;
                  -moz-appearance: none;
                  appearance: none;
                  height: 0;
                  //left: 0;
                  line-height: 0;
                  outline: none;
                  //position: absolute;
                  //top: 0;
                  //width: 0;


                  transition: all 3s linear;


                  &:before,
                  &:after {
                    content: '';
                    position: absolute;
                    box-sizing: border-box;
                  }

                  &:before {
                    border-radius: 22px;
                    //background: green;
                    left: 0;
                    height: 44px;
                    top: -1px;
                    transition: background-color 0.25s ease-out 0.10s;
                    width: 100%;
                    box-sizing: border-box;
                  }

                  &:after {
                    width: 28px;
                    height: 28px;
                    top: 7px;
                    border-radius: 50%;
                    background: #018E37;
                    transform: translate(0, 0);
                    transition: transform 0.25s ease-out 0.10s;
                    background: #FFFFFF;
                    //box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50);
                    box-sizing: border-box;
                    left: 10px;
                    background-image: url("../../assets/img/add-button.svg");
                    background-repeat: no-repeat;
                    background-position: center;
                  }

                  &:checked {

                    &:after {
                      //left: auto;
                      //right: 10px;
                      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.50);
                      transform: translate(87px, 0);
                      background-image: none;
                    }

                    & ~ .lbl-off {
                      opacity: 0;
                    }

                    & ~ .lbl-on {
                      opacity: 1;
                    }
                  }

                  &.switch {
                    &:checked:before {
                      background: #018E37;
                    }
                  }

                  &#switch-blue {
                    &:checked:before {
                      background: white;
                    }
                  }
                }
              }

              @media (max-width: 575.98px) {
                margin-top: 10px;
              }
            }

            .old-price {
              font-family: Rubik-Regular;
              font-size: 20px;
              color: #63666B;
              text-align: center;
              text-decoration: line-through;
            }

            .new-price {
              font-family: Rubik-Medium;
              font-size: 26px;
              color: #383B4B;
              letter-spacing: -0.65px;
              text-align: center;
            }

            .add-btn {
