
.lpw_body_container {
  .center-v-h {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .full-width-bg {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }

  a {
    text-decoration: none;
  }

  .d-none {
    display: none;
  }

  .px-0 {
    padding-left: 0;
    padding-right: 0;
  }

  .pl-15 {
    padding-left: 15px;
  }

  .px-15 {
    padding-left: 15px;
    padding-right: 15px;
  }

  .body-text {
    font-family: Roboto-Regular;
    font-size: 18px;
    color: #84888F;
    text-align: center;
    line-height: 30px;
  }

  .body-text-bold {
    font-family: Roboto-Medium;
    font-size: 18px;
  }


  .label-text {
    font-family: Roboto-Medium;
    font-size: 16px;
    color: #ACB1B9;
    font-weight: normal;

    @media (max-width: 575.98px) {
      font-size: 15px;
    }
  }

  .title-1-thin {
    font-family: Roboto-Light;
    font-size: 35px;
    color: #333333;
    letter-spacing: 0;
  }

  .rubik-light{
    font-family: Rubik-Light !important;
  }
  .rubik-regular{
    font-family: Rubik-Regular !important;
  }

  .subtitle-1-bold {
    font-family: Roboto-Bold;
    font-size: 35px;
    letter-spacing: -1px;
  }

  .subtitle {
    font-family: Roboto-Medium;
    font-size: 23px;
    color: #64666F;
    letter-spacing: -0.66px;

    @media (max-width: 575.98px) {
      font-size: 20px;
    }
  }


  //colors

  a.btn {
    display: flex !important;;
    justify-content: center !important;;
    align-items: center !important;;
  }

  .btn {
    padding: 6px 30px !important;
    font-family: Roboto-Medium !important;
    font-size: 15px !important;
    text-align: center !important;
    height: 46px !important;

    &, &:hover, &:focus {
      outline: none !important;
      box-shadow: none !important;
    }
  }

  .btn-success {
    background-color: #018E37 !important;
    color: #fff !important;
    border-color: #018E37 !important;
    border-radius: 3px !important;
    outline: none !important;
    box-shadow: none !important;

    &:hover, &:focus {
      background-color: #2bb012 !important;
      border-color: #2bb012 !important;
      outline: none !important;
      box-shadow: none !important;
    }
  }

  .btn-outline-success {
    background-color: #fff !important;
    color: #018E37 !important;
    border-color: #018E37 !important;
    border-radius: 3px !important;
    outline: none !important;
    box-shadow: none !important;

    &:hover, &:focus {
      background-color: #018E37 !important;
      color: #ffffff !important;
      outline: none !important;
      box-shadow: none !important;
    }
  }

  .btn-light {
    background: #FFFFFF !important;
    border-radius: 3px !important;
    font-family: Roboto-Medium !important;
    font-size: 15px !important;
    color: #202330 !important;
    text-align: center !important;

    &:hover, &:focus {
      background: #2bb012 !important;
      color: #ffffff !important;
    }
  }


  .features-list {
    list-style: none;
    padding-left: 0;

    li {
      position: relative;
      padding-left: 30px;
      margin-bottom: 10px;
      font-family: Roboto-Regular;
      font-size: 16px;
      line-height: 20px;
      font-weight: normal;

      strong {
        font-family: Roboto-Bold;
      }

