  .custom-radio {
    display: block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 12px;
    cursor: pointer;
    user-select: none;

    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;

      &:checked ~ .checkmark {
        background-color: #fff;
        border: 1px solid #018E37;

        &:after {
          display: block;
        }
      }
    }

    .checkmark {
      position: absolute;
      //top: 5px;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      height: 20px;
      width: 20px;
      background-color: #fff;
      border: 1px solid #018E37;
      border-radius: 50%;

      &:after {
        content: "";
        position: absolute;
        display: none;
        top: 3px;
        left: 3px;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: #018E37;
      }
    }

    &:hover {
      input ~ .checkmark {
        background-color: #fff;
        border: 1px solid #018E37;
      }
    }
  }


  .package-header-lg {
    .custom-radio {
      padding: 20px 20px;
      border-bottom: 1px solid #E9E9E9;
      margin-bottom: 0;

      .checkmark {
        right: 20px;
        left: auto;
        top: 10px;
        transform: translateY(0);
      }

      .p-label {
        @extend .label-text;
      }

      h1 {
        @extend .subtitle-1-bold;
        color: #202330;
        margin: 0;
        line-height: 1;
      }
    }

  }

  .package-list {
    list-style: none;
    padding: 0 20px;
    margin-bottom: 0;


    li {
      border-bottom: 1px solid #E9E9E9;
      padding: 20px 0;

      &:last-child {
        border-bottom: none;
      }

