  .breadcrumbs-container {
    background-color: #2E3A4D;
    height: 50px;
    padding: 7px 0;
    margin-bottom: 30px;

    .breadcrumb {
      margin-bottom: 0;
      background-color: transparent;

      li {
        &, a {
          font-family: Roboto-Regular;
          font-size: 13px;
          color: #FFFFFF;
          letter-spacing: 0.41px;
          text-decoration: none;
        }

        &:not(:first-child) {
          &:before {
            content: "\003e";
          }
        }
      }

      .active {
        color: #ACB1B9;
      }
    }
  }

  .mobile-nave {
    @media (min-width: 576px) {
      display: none;
    }

    a {
      &, &:hover, &:focus {
        font-family: Roboto-Medium;
        font-size: 17px;
        color: #000000;
        display: flex;
        align-items: center;
        text-decoration: none;
      }

      img {
        margin-right: 5px;
      }
    }
  }

  .form-control {
    font-family: Roboto-Regular;
    font-size: 16px;
    color: #84888F;
    line-height: 20px;
    min-height: 46px;

    background: #FFFFFF;
    border: 1px solid #CFCFCF;
    border-radius: 3px;
    box-shadow: none;
  }

  .alert {
    padding: 5px 10px;
    margin-bottom: 15px;
    font-size: 12px;
    font-family: Roboto-Regular;
  }

  .alert-danger {
    border-color: #F34848;
    background-color: #F34848;
    color: #ffffff;
  }

  .d-none {
    display: none;
  }

  .d-block {
    display: block;
  }

  .d-none-sm {
    @media (min-width: 576px) {
      display: none !important;
    }
  }

  .d-block-sm {
    @media (min-width: 576px) {
      display: block;
    }
  }

  .d-table-sm {
    @media (min-width: 576px) {
      display: table;
    }
  }

  .mt-17 {
    margin-top: 17px;
  }
}
